/* ====== FONT SIZES ====== */
$font-size-small-extra-extra: 9px;
$font-size-small-extra: 11px;
$font-size-small: 12px;
$font-size-medium: 14px;
$font-size-large: 16px;
$font-size-large-extra: 18px;
$font-size-large-extra-extra: 24px;
$font-size-large-extra-extra-extra: 32px;
$font-size-large-extra-extra-extra-extra: 48px;


/* ====== FONT WEIGHTS ====== */
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-semi-bold: 600;
$font-weight-bold: 700;
$font-weight-bold-extra: 800;


/* ====== TRANSITIONS ====== */
@mixin transition($duration) {

  @if $duration == 'fast' {
    transition-duration: 0.3s;
  } @else if $duration == 'medium' {
    transition-duration: 0.6s;
  } @else if $duration == 'slow' {
    transition-duration: 0.9s;
  } @else {
    transition-duration: 0.3s;
  }

  transition-property: all;
  transition-timing-function: ease;
}
