.btn:active:focus,.btn.active:focus,
.btn.focus,.btn:active.focus,.btn.active.focus {
  outline: none !important;
  box-shadow: none !important;
}

.btn {
  // margin: 0 8px 8px 0;
  display: inline-block;
  transition: all 0.2s ease !important;
  border-radius: 8px;
  font-weight: $font-weight-bold;
  border:none;
  font-size: $font-size-medium;
  
  &:hover {
    opacity: 0.8 !important;
	-webkit-opacity: 0.8 !important;
	-moz-opacity: 0.8 !important;
	box-shadow: none !important;
  }

  &:active {
    transform: translate(0,2px);
  }

  /* == SIZES == */
  &--large {
    /* new sizes
	height: 64px !important;
    line-height: 64px !important;*/
    height: 48px !important;
    line-height: 48px !important;
    font-size: $font-size-large !important;
    padding: 0 24px !important;
    
    @media screen and (max-width: 576px) {
		padding: 0 20px !important;    
	}
  }

  &--large__extra {
    height: 64px !important;
    line-height: 64px !important;
    font-size: 20px !important;
    padding: 0 40px !important;
    
    @media screen and (max-width: 576px) {
		padding: 0 20px !important;
		height: 48px !important;
		line-height: 48px !important;
    	font-size: $font-size-large !important;
	}
	
  }
  
  &--medium {
	/* new sizes 
    height: 48px !important;
    line-height: 48px !important;*/
    height: 40px !important;
    line-height: 40px !important;
    font-size: $font-size-large !important;
    padding: 0 20px !important;
    
    &__inline {
	    padding: 0px 16px !important
    }
  }

  &--small {
    height: 32px !important;
    line-height: 32px !important;
    font-size: $font-size-medium !important;
    padding: 0 12px !important;
    
    &__inline {
	    padding: 0px 10px !important
    }
    
    &.btn--icon {
	      &:before {
	        width: 16px;
	        height: 16px;
	        margin-right: 6px;
	      }
	  }
  }

  &--small-extra {
    height: 24px !important;
    line-height: 24px !important;
    font-size: $font-size-small-extra !important;
    padding: 0 8px !important;
    &:before {
      width: 11px !important;
      height: 11px !important;
    }
  }
  
  &--sm-small {
	  @media screen and (max-width: 576px) {
		height: 24px !important;
	    line-height: 24px !important;
	    font-size: $font-size-small-extra !important;
	    padding: 0 8px !important;
	    &:before {
	      width: 11px !important;
	      height: 11px !important;
	    }			  
	  }
  }

  /* == STYLES == */
  &.btn-primary,
  &--primary {
    background: $color-har-blue !important;    
    padding: 0 24px;
    height: 40px;
    line-height: 40px;
    color: $color-snow-white !important;
  }
  &.btn-danger,
  &--prominent {
    background: $color-har-pink !important;
    padding: 0 24px;
    height: 40px;
    line-height: 40px;
    color: $color-snow-white !important;

    &:hover {
      color: $color-snow-white;
    }
  }

  &--simple ,
  &--ordinary {
    background: $color-snow-white !important;
    border: 1px solid #d0d0d0 !important;
    padding: 0 24px;
    height: 40px;
    line-height: 40px;
    color: $color-black;

    &:hover {
      color: $color-black;
    }
  }

  &--shapeless {
    background: transparent !important;
    border: 1px solid rgba(255,255,255,0) !important;
    padding: 0 24px;
    height: 40px;
    line-height: 40px;
    color: $color-black;

    &:hover {
      color: $color-har-blue;
    }
  }

  &:disabled {
    opacity: 0.65 !important;
    background: $color-cement !important;
    border: 1px solid $color-cement !important;
    color:  $color-cement-dark !important;
    cursor: not-allowed;

    &:hover {
      color: $color-auxiliary;
    }
  }

  &--search {
    width: 24px !important;
    height: 24px !important;
    background: #fff url(https://content.harstatic.com/media/icons/styleguide/icon-search.svg) center no-repeat;
    padding: 0 !important;
    border-radius: 50%;
  }
    
  &--flate {
	height: 40px;
    color: $color-black !important;
    
    &:hover {
      background-color: $color-cement-light;
      opacity: 1 !important;
	  -webkit-opacity: 1 !important;
	  -moz-opacity: 1 !important;
    }
  }
  
  &--text {
    height: inherit;
    font-weight: $font-weight-bold;
    color: $color-black !important;
    box-shadow: 0 0px 0 $color-har-blue-dark !important;
    
    &:hover {
      background-color:transparent;
      opacity: 1 !important;
	  -webkit-opacity: 1 !important;
	  -moz-opacity: 1 !important;
    }
  }
  
  &--filters {
	height: 40px;
    font-size: $font-size-medium;
    color: $color-carbon !important;
    box-shadow: 0 0px 0 $color-har-blue-dark !important;
    padding: 0 10px !important;
    display: flex;
    -ms-flex-align: center !important;
	align-items: center !important;
	cursor: pointer;

    &:hover {
      background-color: $color-cement-light;
      opacity: 1 !important;
	  -webkit-opacity: 1 !important;
	  -moz-opacity: 1 !important;
    }
    
    &-active {
	  background-color: $color-cement-light;  
    }
    
    img {
		margin-right: 3px;
    }
    
    span {
	    display:inline-block;
	    height: 16px;
	    border-radius: 8px;
	    line-height:16px;
	    font-weight: $font-weight-bold;
	    color : $color-snow-white;
	    text-align: center;
	    background-color: $color-har-pink;
	    min-width: 16px;
	    font-size: $font-size-small;
	    margin-left: 4px;
	    padding-left: 4px;
	    padding-right: 4px;
	    margin-top: -2px;
    }
    
  }
  
  &--outline {
	  display: inline-block;
	  border-radius: 2px;
	  border: solid 1px $color-auxiliary-light;
	  background-color: #ffffff;
	  color: $color-auxiliary !important;
	  font-weight: $font-weight-semi-bold;
	  font-size:$font-size-medium;
	  height: 27px;
	  line-height: normal;
	  padding:4px 6px 2px 6px;
	  
	  img {
		  float: left;
		  margin-top: 2px;
		  margin-right: 4px;
	  }
	  
	  &.editfolder {
		  border: none;
		  color: $color-har-blue !important;
		  font-weight: normal;
	  }
  }
  
  
  &--claimprofile {
		background-color: $color-har-blue-dark;
		color: $color-snow-white !important;
		font-size: $font-size-small;
		border-left: 4px solid #fd4b78 !important;
		height: 39px;
		padding:0px 20px !important;
		white-space: nowrap;
		line-height: 39px ;
		
		&__large {
			display: flex;			
			font-size: $font-size-large;
			font-weight: normal !important;
			background-color: transparent;
			border-left: none !important;
			padding: 0px !important;
			border-radius: 0px !important;
			
			@media screen and (max-width: 500px) {
				font-size: $font-size-small;
			}
			
			&__title {
				color: $color-snow-white;
				height: 40px;
				background-color: $color-har-pink;			
				border-right: 1px solid #be3759;
				padding: 0px 10px 0px 16px;
				font-weight: bold;
				border-radius: 4px 0px 0px 4px;
			}
			
			&__text {
				padding-left: 10px;
				color: $color-snow-white;
				height: 40px;
				background-color: $color-har-pink;
				padding: 0px 3px 0px 10px ;
				border-radius: 0px;
			}
		}
	}
  
