 
/* gallery css */
 .photo_gallery {
    position:fixed;
    top:0px;
    left:0px;
    right:0px;
    bottom:0px;
    background-color:#ffffff;
    z-index:1111;
}
 .photo_gallery > * {
    outline:none !important;
}
 .ph_header {
    height:80px;
     box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
    border: solid 1px #dfe3f0;
    background-color: #ffffff;
    position:absolute;
     left:0px;
    right:0px;
     top:0px;
    z-index:1112;
}
 .ph_content {
    position: absolute;
    left:0px;
    right:0px;
    left:0px;
    top:80px;
    overflow-y:auto;
    overflow-x:hidden;
    -webkit-overflow-scrolling: touch;
     padding:60px;
    bottom:0px;
}
 .ph_item_dsc {
    font-size:11px;
    width:100%;
    position: absolute;
    left:0px;
    right:0px;
    top:auto;
    bottom:0px;
    background: rgb(0,0,0);
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 90%);
    padding:25px 10px 10px 10px;
    color:#fff;
}
 .ad_row {
    flex-grow: 10;
     padding:0px;
    height:110px !important;
    width:100%;
    text-align: center;
}
 .ad_row img {
    max-width:100%;
     max-height:none;
    object-fit:none !important;
     min-height:90px !important;
}
 .play_image {
    background-position: center center;
     background-size: cover;
     background-repeat: no-repeat;
    display: block;
}
 ul.ph_gallery_cntr {
    display: flex;
    flex-wrap: wrap;
}
 .ph_gallery_cntr > li.ad_inline_gallery, .ph_gallery_cntr > li.ph_gallery_cntr_item {
    height:360px;
    flex-grow: 1;
    margin:5px;
    position: relative;
    flex-grow:1 1 1;
    cursor:pointer;
     background:#EDF0FA;
    min-width:200px;
}
 .ph_gallery_cntr > li.ph_gallery_cntr_item:nth-last-child(-n+2) {
     max-width:33%;
}
 .ph_gallery_cntr > li.ad_inline_gallery:last-child, .ph_gallery_cntr > li.ph_gallery_cntr_item:last-child {
    flex-grow: 10;
}
 .ph_gallery_cntr > li.ph_gallery_cntr_item > img {
    max-height: 100%;
    min-width: 100%;
     object-fit: cover;
    vertical-align: bottom;
}
 .ph_gallery_cntr > li.ad_inslides {
    width:100%;
    text-align: center;
    height:90px;
}
 .ph_gallery_cntr > li.ad_inslides img {
    min-width:auto;
}
 @media (max-width:1920px) {
    .ph_gallery_cntr > li.ph_gallery_cntr_item,.ph_gallery_cntr > li.ad_inline_gallery {
        height:320px;
    }
}
 @media (max-width:1366px) {
    .ph_gallery_cntr > li.ph_gallery_cntr_item,.ph_gallery_cntr > li.ad_inline_gallery {
        height:250px;
    }
}
 @media (max-width:1000px) {
    .ph_gallery_cntr > li.ph_gallery_cntr_item,.ph_gallery_cntr > li.ad_inline_gallery {
        height:200px;
    }
}
 @media (max-width:800px) {
    .ph_gallery_cntr > li.ph_gallery_cntr_item,.ph_gallery_cntr > li.ad_inline_gallery {
        height:170px;
    }
}
 @media (max-width:600px) {
    .ph_gallery_cntr > li.ph_gallery_cntr_item,.ph_gallery_cntr > li.ad_inline_gallery {
        height:150px;
    }
}
 @media (max-width:500px) {
    .ph_gallery_cntr > li.ph_gallery_cntr_item,.ph_gallery_cntr > li.ad_inline_gallery {
        height:105px;
    }
}
