 .gallery_slider {
    position:fixed;
    left:0px;
    right:0px;
    left:0px;
    top:0px;
    z-index: 1114;
    bottom:0px;
}
 .gallery_slides {
     margin:auto;
    height: 100vh;
    display: flex;
    max-width:1222px;
    height:100%;
}
 .gallery_slider .slick-prev:before {
    background: url("https://content.harstatic.com/media/icons/gallery_arrow_left_white.svg") no-repeat center center;
    width:56px;
    height:56px;
}
 .gallery_slider .slick-next:before {
    background: url("https://content.harstatic.com/media/icons/gallery_arrow_right_white.svg") no-repeat center center;
    width:56px;
    height:56px;
}
 .gallery_slider .slick-prev,.gallery_slider .slick-next{
    width:56px;
    height:56px;
}
 .gallery_slider .slick-prev{
    left:-56px;
}
 .gallery_slider .slick-next{
    right:-56px;
}
 .gs_item {
    background-color: #000000;
    border-radius:4px;
    height:calc(100vh - 36px);
    width:100%;
     max-width:1222px;
    margin:16px auto;
    display: flex !important;
    overflow:hidden;
}
 .gs_item_img,.gs_item_content{
}
 .gs_item_img {
    background-position:center center;
    background-repeat: no-repeat;
     background-size:contain;
    width: calc(100% - 282px);
    height:100%;
}
 .gs_item_content {
    left:auto;
    right:0px;
    width:282px;
    bottom:0px;
    top:0px;
    background-color: #ffffff;
    padding:32px 16px 16px 16px;
    position:relative;
    border-radius:0px 4px 4px 0px;
}
 .ad_placeholder {
    margin-top: auto;
    position: absolute;
    bottom:16px;
    left:16px;
    right:16px;
}
 .closr_full_screen {
    position: absolute;
    right:0px;
    top:0px;
    text-align:center;
    width:40px;
    height:40px;
    line-height:40px;
    display: block;
    border-radius:50%;
}
 .gallery_slides img {
    display:inline-block;
}
